<template>
	<div class="xui_item">
	    <label :class="{xui_input_required: isSearch}"><slot></slot></label>
	    <div class="xui_content" :class="{xui_input_search: isNecessary}">
	        <input 
		        :type="type" 
		        :readonly="readonly" 
		        :class="styleType"
		        v-model.lazy="currentValue"
		        :disabled="disabled" 
		        class="xui_input" 
		        ref="input"
		        :maxlength="limit"
		        :title="currentValue"
		        @keyup="handleFilter($event)"
		        @click="handleClick($event)"
		        :placeholder="placeholder" 
	        />
	    </div>
	</div>
</template>

<script type="text/javascript">
	export default{
		data(){
			return{
				styleType: '',
			}
		},
		props: ['type', 'label', 'placeholder', 'isNecessary', 'isSearch', 'readonly', 'disabled', 'styles', 'value', 'limit', 'self'],
		mounted(){
			this.$nextTick(function(){
				this.styleType += this.styles || '';
			})
		},
		computed:{
			currentValue:{
				set(){
		        },
		        get(){
		           return this.value
		        },
			}
		},
		watch:{
			value(){
			}
		},
		methods: {
			handleChange(e){
				this.$emit('change', e);
			},
			handleFilter(e){
				this.$emit('handleFilter', e);
			},
			handleClick(e){
				this.$emit('click', e);
			}
		},
	}
</script>

<style type="text/css">
	.xui_item{
	    display: flex;
	    flex-direction: initial;
	    justify-content: flex-start;
	    align-items: center;
	}
	.xui_content{
	    position: relative;
	}
	input[disabled],button[disabled] {
	  cursor: not-allowed;
	}
	.xui_input{
	    position: relative;
	    width: 200px;
	    box-sizing: border-box;
	    padding: .37em .57em;
	    margin: .57em;
	    border: 1px solid #dbdbdb;
	    border-radius: .28em;
	    font-size: 1em;
	    line-height: 1.4;
	    outline: none;
	    -webkit-transition: all 0.2s;
	    transition: all 0.2s;
	}
	.xui_input:focus,.xui_textarea:focus{
	    border-color: #87cba3;
	}
	.xui_input_required::before{
	    content: '*';
	    color: red;
	}
	.xui_input_search::before{
	    content: '';
	    width: 20px;
	    height: 20px;
	    display: block;
	    position: absolute;
	    top: 50%;
	    left: 1em;
	    margin-top: -10px;
	    background: url(data:image/png;base64,) no-repeat center center;
	    background-size: contain;
	    z-index: 9999;
	}
	.xui_input_search .xui_input{
	    text-indent: 1.5em;
	}

</style>


