<template>
    <div class="xui_star" :class="sizeArr[size]">
        <span class="xui_star_dark"  
            v-for="item in (allStars || defaultStars)"
            :class="{xui_star_light: item <= activeStars}"
            >
        </span>
    </div>
</template>

<script type="text/javascript">
    export default{
        name: 'xui_star',
        data(){
            return{
                defaultStars: 5,
                sizeArr: ['xui_star_small', 'xui_star_big'],
            }
        },
        props:['activeStars', 'allStars', 'size'],
        mounted(){
            this.$nextTick(function(){

            })
        }
    }
</script>

<style type="text/css">
    .xui_star{
        display: flex;
    }
    .xui_star span{
        display: block;
        width: 24px;
        height: 24px;
    }
    .xui_star.xui_star_big span{
        width: 32px;
        height: 32px;
    }
    .xui_star.xui_star_small span{
        width: 12px;
        height: 12px;
    }
    .xui_star .xui_star_dark{
        background: url(data:image/png;base64,) no-repeat center center;
        background-size: 100% 100%;
    }
    .xui_star .xui_star_light{
        background: url(data:image/png;base64,) no-repeat center center;
        background-size: 100% 100%;
    }
</style>


